.App {
  font-family:serif;
  background:rgb(247, 247, 244) ;
}


@import url("https://fonts.googleapis.com/css?family=Tangerine&display=swap");

.App-header {
  text-align: center;
  display: flex;
  justify-content: center;
  color: black;
  background: #f7c6bd;
  font-family: "Tangerine", serif;
}

.App-header p {
  position: relative;
  font-size: calc(30px + 2vmin);
  padding: 1rem 2rem;
  border: 6px double #000;
  text-shadow: 4px 4px 4px rgb(109, 109, 109);
}

.button{
  color: #fff;
  background: #00aaff;
}

.button:hover{
  background: #3b82c4;
}

.main{
  font-size: calc(10px + 2vmin);
}

.form{
  display: flex;
  justify-content: center;
  margin: 30px;
}

.form-group{
  margin-left: 20px;
}

.data{
  display: flex;
  justify-content: flex-start;
  margin: calc(50px + 3vmin) calc(70px + 28vmin);
}
.data p,a{
  margin-left: 30px;
}